'use strict';

var request = require('request'); // TODO: this is pretty brittle; if libgen changes how they format
// results it will break


var LATEST_ID_REGEX = /<td>[0-9]+<\/td>/g;
module.exports = {
  id: function id(mirror, callback) {
    var httpOptions = {
      url: "".concat(mirror, "/search.php?mode=last")
    };
    request(httpOptions, function (err, response, body) {
      if (err) {
        console.error('Request error');
        return callback(err);
      } else if (response.statusCode !== 200) {
        return callback(new Error("Bad reponse code (".concat(response.statusCode, ") for ").concat(httpOptions.url)));
      }

      var idsResults = body.match(LATEST_ID_REGEX);
      var latestId = idsResults[0].replace(/[^0-9]/g, "");
      if (!parseInt(latestId)) return callback(new Error("Failed to return a numeric ID: ".concat(latestId))); // returning a string rather than a number because once the IDs
      // get higher JS might start mangling them

      return callback(null, latestId);
    });
  },
  text: function text(mirror, callback) {
    this.id(mirror, function (err, data) {
      if (err) return callback(err);
      var httpOptions = {
        url: "".concat(mirror, "/json.php?ids=").concat(data, "&fields=*")
      };
      request(httpOptions, function (err, response, body) {
        if (err) return callback(err);else if (response.statusCode !== 200) return callback(new Error("Bad response code: ".concat(response.statusCode))); // the API always returns an array of objects, so for
        // functions like this which are mean to return a single text,
        // we pick out the first (only) element in the array
        // automatically

        return callback(null, JSON.parse(body)[0]);
      });
    });
  }
};